import React, { useState, useMemo, useEffect } from "react";

interface GraphNode {
    id: string;
    label: string;
    color: string;
    type: "hub" | "node" | "sub";
    parentId?: string;
    level: number;
    angle: number; // in degrees
    distance: number;
    size: number;
    status?: "active" | "merged" | "default";
    hasInnerRing?: boolean;
    expanded?: boolean;
    children?: GraphNode[];
}

const CATEGORIES = {
    KNOWLEDGE: "#4ade80",
    AUTOMATION: "#f97316",
    COMMUNICATION: "#3b82f6",
    SECURITY: "#ef4444",
    CREATOR: "#ec4899",
    FINANCE: "#eab308",
    META: "#22d3ee",
    CREATIVITY: "#8b5cf6",
};

const INITIAL_NODES: GraphNode[] = [
    { id: "core", label: "MUSE CORE", color: "#fbbf24", type: "hub", level: 0, angle: 0, distance: 0, size: 45 },

    // Knowledge & Research (Green)
    { id: "k_hub", label: "知識與研究", color: CATEGORIES.KNOWLEDGE, type: "node", level: 1, angle: 110, distance: 200, size: 28, status: "active" },
    { id: "k1", label: "TD 學院管理", color: CATEGORIES.KNOWLEDGE, parentId: "k_hub", type: "sub", level: 2, angle: 130, distance: 320, size: 14, status: "active" },
    { id: "k2", label: "TD 字幕轉錄校正", color: CATEGORIES.KNOWLEDGE, parentId: "k_hub", type: "sub", level: 2, angle: 90, distance: 340, size: 12, status: "merged" },
    { id: "k3", label: "料理實驗室", color: CATEGORIES.KNOWLEDGE, parentId: "k_hub", type: "sub", level: 2, angle: 150, distance: 340, size: 12 },
    { id: "k4", label: "健康追踪器", color: CATEGORIES.KNOWLEDGE, parentId: "k_hub", type: "sub", level: 2, angle: 70, distance: 320, size: 12, hasInnerRing: true },

    // Automation (Orange)
    { id: "a_hub", label: "自動化工作流", color: CATEGORIES.AUTOMATION, type: "node", level: 1, angle: 40, distance: 180, size: 30, status: "active" },
    { id: "a1", label: "Swingvy 預設自動化", color: CATEGORIES.AUTOMATION, parentId: "a_hub", type: "sub", level: 2, angle: 60, distance: 300, size: 18, status: "active" },
    { id: "a2", label: "Sub-Agent 團隊管理", color: CATEGORIES.AUTOMATION, parentId: "a_hub", type: "sub", level: 2, angle: 25, distance: 320, size: 20, hasInnerRing: true },
    { id: "a3", label: "Gmail 郵件系統", color: CATEGORIES.AUTOMATION, parentId: "a_hub", type: "sub", level: 2, angle: 10, distance: 340, size: 15 },
    { id: "a4", label: "收官反思", color: CATEGORIES.AUTOMATION, parentId: "a_hub", type: "sub", level: 2, angle: 80, distance: 300, size: 12 },

    // Communication (Blue)
    { id: "c_hub", label: "通訊管理員", color: CATEGORIES.COMMUNICATION, type: "node", level: 1, angle: 330, distance: 220, size: 26, status: "active" },
    { id: "c1", label: "Telegram 富文本", color: CATEGORIES.COMMUNICATION, parentId: "c_hub", type: "sub", level: 2, angle: 350, distance: 350, size: 12, status: "active" },
    { id: "c2", label: "Slack 操作 SOP", color: CATEGORIES.COMMUNICATION, parentId: "c_hub", type: "sub", level: 2, angle: 310, distance: 360, size: 10, status: "merged" },
    { id: "c3", label: "iMessage 管理", color: CATEGORIES.COMMUNICATION, parentId: "c_hub", type: "sub", level: 2, angle: 335, distance: 380, size: 12 },

    // Security (Red)
    { id: "s_hub", label: "安全自檢", color: CATEGORIES.SECURITY, type: "node", level: 1, angle: 290, distance: 240, size: 24, hasInnerRing: true },
    { id: "s1", label: "1password", color: CATEGORIES.SECURITY, parentId: "s_hub", type: "sub", level: 2, angle: 270, distance: 380, size: 14 },
    { id: "s2", label: "敏感資訊守衛", color: CATEGORIES.SECURITY, parentId: "s_hub", type: "sub", level: 2, angle: 305, distance: 340, size: 12, status: "merged" },

    // Creator (Pink)
    { id: "cr_hub", label: "統一音源中繼", color: CATEGORIES.CREATOR, type: "node", level: 1, angle: 245, distance: 200, size: 25, status: "active" },
    { id: "cr1", label: "YouTube 字幕提取", color: CATEGORIES.CREATOR, parentId: "cr_hub", type: "sub", level: 2, angle: 225, distance: 350, size: 12 },
    { id: "cr2", label: "提示注入防禦", color: CATEGORIES.CREATOR, parentId: "cr_hub", type: "sub", level: 2, angle: 265, distance: 320, size: 14, status: "active" },
];

const RadialGraph: React.FC = () => {
    const [nodes, setNodes] = useState<GraphNode[]>(INITIAL_NODES);
    const [expandedIds, setExpandedIds] = useState<Set<string>>(new Set(["k_hub", "a_hub", "c_hub", "s_hub", "cr_hub"]));
    const [center, setCenter] = useState({ x: 500, y: 400 });

    useEffect(() => {
        const updateCenter = () => {
            setCenter({ x: window.innerWidth / 2, y: window.innerHeight / 2 });
        };
        updateCenter();
        window.addEventListener("resize", updateCenter);
        return () => window.removeEventListener("resize", updateCenter);
    }, []);

    const toggleExpand = (id: string) => {
        setExpandedIds(prev => {
            const next = new Set(prev);
            if (next.has(id)) next.delete(id);
            else next.add(id);
            return next;
        });
    };

    const visibleNodes = useMemo(() => {
        return nodes.filter(n => {
            if (n.level === 0 || n.level === 1) return true;
            if (n.parentId && expandedIds.has(n.parentId)) return true;
            return false;
        });
    }, [nodes, expandedIds]);

    const getPos = (node: GraphNode) => {
        const rad = (node.angle * Math.PI) / 180;
        return {
            x: center.x + node.distance * Math.cos(rad),
            y: center.y - node.distance * Math.sin(rad),
        };
    };

    return (
        <div className="w-full h-full relative select-none">
            <svg className="w-full h-full pointer-events-none overflow-visible">
                <defs>
                    {visibleNodes.map(n => (
                        <filter key={`glow-${n.id}`} id={`filter-${n.id}`} x="-100%" y="-100%" width="300%" height="300%">
                            <feGaussianBlur in="SourceAlpha" stdDeviation={n.size / 4} result="blur" />
                            <feFlood floodColor={n.color} floodOpacity="0.6" result="color" />
                            <feComposite in="color" in2="blur" operator="in" result="glow" />
                            <feMerge>
                                <feMergeNode in="glow" />
                                <feMergeNode in="SourceGraphic" />
                            </feMerge>
                        </filter>
                    ))}
                    <radialGradient id="coreGradient">
                        <stop offset="0%" stopColor="#ffd700" />
                        <stop offset="50%" stopColor="#fbbf24" />
                        <stop offset="100%" stopColor="#b45309" />
                    </radialGradient>
                </defs>

                {/* Edges */}
                {visibleNodes.map(n => {
                    if (n.level === 0) return null;
                    const parent = visibleNodes.find(p => p.id === (n.parentId || "core"));
                    if (!parent) return null;

                    const start = getPos(parent);
                    const end = getPos(n);

                    return (
                        <line
                            key={`edge-${n.id}`}
                            x1={start.x}
                            y1={start.y}
                            x2={end.x}
                            y2={end.y}
                            stroke={n.color}
                            strokeWidth={n.level === 1 ? 1.5 : 1}
                            strokeOpacity={0.3}
                            strokeDasharray={n.level === 2 ? "4 4" : "none"}
                        />
                    );
                })}

                {/* Nodes */}
                {visibleNodes.map(n => {
                    const pos = getPos(n);
                    const isCore = n.id === "core";

                    return (
                        <g
                            key={`node-g-${n.id}`}
                            style={{ cursor: "pointer", pointerEvents: "auto" }}
                            onClick={() => toggleExpand(n.id)}
                            className="group"
                        >
                            {/* Glow */}
                            <circle
                                cx={pos.x}
                                cy={pos.y}
                                r={n.size}
                                fill={isCore ? "url(#coreGradient)" : (n.status === "merged" ? "#334155" : n.color)}
                                filter={`url(#filter-${n.id})`}
                                className="transition-all duration-500 hover:brightness-125"
                                opacity={n.status === "merged" ? 0.3 : (n.status === "active" ? 1 : 0.8)}
                            />

                            {/* Inner Ring (Dashed) */}
                            {n.hasInnerRing && (
                                <circle
                                    cx={pos.x}
                                    cy={pos.y}
                                    r={n.size * 1.3}
                                    fill="none"
                                    stroke={n.color}
                                    strokeWidth={1}
                                    strokeDasharray="4 4"
                                    opacity={0.5}
                                    className="animate-[spin_20s_linear_infinite]"
                                />
                            )}

                            {/* Node Center */}
                            <circle
                                cx={pos.x}
                                cy={pos.y}
                                r={n.size * 0.4}
                                fill="white"
                                fillOpacity={n.status === "active" ? 0.4 : 0.2}
                                className="pointer-events-none"
                            />

                            {/* Label */}
                            <text
                                x={pos.x}
                                y={pos.y + n.size + 20}
                                textAnchor="middle"
                                fill="white"
                                className="text-[14px] font-bold tracking-tight drop-shadow-lg pointer-events-none select-none"
                                style={{ textShadow: "0 0 10px rgba(0,0,0,1)" }}
                            >
                                {n.label}
                                {n.size > 25 && " ★"}
                            </text>

                            {/* Version Tag */}
                            {n.level > 0 && n.expanded !== false && (
                                <text
                                    x={pos.x}
                                    y={pos.y - n.size - 10}
                                    textAnchor="middle"
                                    fill="#94a3b8"
                                    className="text-[10px] font-bold opacity-60 select-none"
                                >
                                    v1.0
                                </text>
                            )}
                        </g>
                    );
                })}
            </svg>

            {/* Central Icon Placeholder */}
            <div
                className="absolute pointer-events-none flex items-center justify-center"
                style={{
                    left: center.x - 25,
                    top: center.y - 25,
                    width: 50,
                    height: 50
                }}
            >
                <div className="w-10 h-10 rounded-lg bg-[#02040a] border border-amber-500 flex items-center justify-center overflow-hidden">
                    <span className="text-amber-500 font-black text-xs">MUSE</span>
                </div>
            </div>
        </div>
    );
};

export default RadialGraph;
